.App {
  text-align: center;
}

ul {
  display: block;
  list-style-type: disc;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  padding-inline-start: 0px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.time-button {
  border-radius: 10px;
  border: 2px solid rgb(25, 118, 210);
  color: black;
  background: white;
  cursor: pointer;
  min-width: 60px;
  min-height: 30px;
  margin: 5px;
}

.timeslots-container {
  margin: 5px;
}

.date-buttons-container{
  margin: 20px;
}

.select-button {
  border-radius: 10px;
  background: rgb(25, 118, 210);
  color: white;
  border: none;
  outline: none;
  cursor: pointer;
  margin: 5px;
  height: 30px;
}

.back-button {
  border-radius: 15px;
  background: rgb(25, 118, 210);
  color: white;
  border: none;
  outline: none;
  cursor: pointer;
  margin: 5px;
  min-width: 100px;
  min-height: 30px;
}

.date-button {
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  border: 2px solid rgb(25, 118, 210);
  color: black;
  background: white;
  cursor: pointer;
  min-width: 90px;
  min-height: 48px;
  margin: 2px;
}

.date-button.selected {
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  border: 2px solid rgb(25, 118, 210);
  color: white;
  background: rgb(25, 118, 210);
  min-width: 90px;
  min-height: 48px;
  cursor: pointer;
}

.date-values-container {
  margin: 10px;
}

.specialist-container {
  margin: 10px;
  display: flex;
  flex-direction: column;
  align-items:first baseline;
}

.classification {
  font-size: 15px;
  color: rgba(0, 0, 0, 0.56);
}

.date {
  font-size: 20px;
  margin: 15px;
}

.App-header {
  background-color: var(--tg-theme-bg-color);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: var(--tg-theme-text-color);
  margin: 10px;
}

.App-link {
  color: #61dafb;
}

.Tabs-container{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 10px;
}

.Tab-service{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 10px;
  border-bottom: solid;
  border-color: var(--tg-theme-button-color);
  border-width: 1px;
  margin: 10px;
  width: 100%;
  min-height: 50px;
  box-sizing: border-box;
  padding: 8px 16px;
  text-align: center;
}

.Tab-specialist{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 10px;
  border-bottom: solid;
  border-color: var(--tg-theme-button-color);
  border-width: 1px;
  margin: 10px;
  width: 100%;
  min-height: 50px;
  box-sizing: border-box;
  padding: 8px 16px;
  text-align: center;
}

.Tab-date{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 10px;
  border-bottom: solid;
  border-color: var(--tg-theme-button-color);
  border-width: 1px;
  margin: 10px;
  width: 100%;
  min-height: 50px;
  box-sizing: border-box;
  padding: 8px 16px;
  text-align: center;
}

.Tab-logo{
  min-width: 56px;
  color: rgba(0, 0, 0, 0.54);
}

.Tab-text{
  display: flex;
  align-items: center;
  flex-direction: column;
  margin: 5px;
}

.Tab-button{
  margin: 5px;
}

.selected-date{
  font-size: 12px;
  color: rgba(0, 0, 0, 0.6);
}

.specialist-buttons-container{
  display: flex;
  align-items: center;
  flex-direction: column;
  margin: 5px;
}

.specialist{
  font-size: 18px;
  margin: 15px;
  width: 240px;
  max-width: 240px;
  text-align: left;
}

.specialist-photo{
  width: 130px;
  height: auto;
  border-radius: 15px;
}

.descr-button{
  border: none;
  background-color: white;
}

.specialist-photo-big{
  width: 200px;
  height: auto;
  border-radius: 15px;
}

.descr-page-container{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.service{
  font-size: 18px;
  margin: 15px;
  width: 210px;
  max-width: 240px;
  text-align: left;
}

.logo{
  width: 200px;
  height: 100px;
}

.mainbutton{
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  border: 2px solid rgb(25, 118, 210);
  color: white;
  background: rgb(25, 118, 210);
  cursor: pointer;
  width: 100%;
  min-height: 48px;
  font-size: 20px;
}

.specialist-block{
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.54);
  color: black;
  background: white;
  width: 100%;
  min-height: 200px;
  margin-top: 10px;
  font-size: 20px;
}

.bonus-block{
  border: 1px solid rgba(0, 0, 0, 0.54);
  width: 100px;
  height: 50px;
  justify-self: center;
}

.specialist-description{
  font-size: 15px;
  color: rgba(0, 0, 0, 0.56);
}

.service-block {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.54);
  color: black;
  background: white;
  width: 100%;
  min-height: 100px;
  margin-top: 10px;
  font-size: 20px;
}

.specialist-button{
  border-radius: 4px;
  border: 2px solid rgb(25, 118, 210);
  color: black;
  background: white;
  cursor: pointer;
  margin: 10px;
  font-size: 20px;
  min-width: 30px;
  min-height: 30px;
}

.specialist-button.selected{
  border-radius: 4px;
  border: 2px solid rgb(25, 118, 210);
  color: white;
  background: rgb(25, 118, 210);
  cursor: pointer;
  margin: 10px;
  font-size: 20px;
  min-width: 30px;
  min-height: 30px;
}

.names-container{
  margin: 5px;
}

.price{
  color: green;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
